"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Form, FormItem, FormLabel, FormControl } from "./ui/form";

export default function Header() {
  const [showPopup, setShowPopup] = useState(false);
  const form = useForm();
  const onSubmit = (data: any) => {
    console.log(data);
  };

  const handleLoginClick = () => {
    setShowPopup(true);
  };

  const closePopup = () => {
    setShowPopup(false);
  };

  return (
    <>
      <header className="bg-white shadow-md">
        <div className="container mx-auto flex items-center justify-between py-4 px-6">
          {/* Logo */}
          <div className="text-blue-600 text-2xl font-bold">
            <Button asChild variant="link">
              <a href="/">Logo</a>
            </Button>
          </div>

          {/* Navigation Links */}
          <nav className="hidden md:flex space-x-6">
            <Button asChild variant="link">
              <a href="/">Home</a>
            </Button>
            <Button asChild variant="link">
              <a href="/upcoming-marathons">Upcoming Marathons</a>
            </Button>
            <Button asChild variant="link">
              <a href="/how-it-works">How It Works</a>
            </Button>
            <Button asChild variant="link">
              <a href="/contact-us">Contact Us</a>
            </Button>
          </nav>

          <div className="flex items-center space-x-4">
            {/* Login Button */}
            <Button variant="ghost" onClick={handleLoginClick}>
              Login
            </Button>

            {/* Register Button */}
            <Button asChild className="bg-green-600">
              <a href="/register">Register</a>
            </Button>
          </div>
        </div>
      </header>

      {/* Popup */}
      {showPopup && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-70 z-50">
          <div className="bg-white p-6 rounded-lg shadow-lg w-96">
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
                <h2 className="text-xl font-bold mb-4">Login</h2>
                <p className="text-gray-600 mb-4">
                  Enter your login and password to continue.
                </p>
                {/* Email Input */}
                <FormItem>
                  <FormLabel>Email</FormLabel>
                  <FormControl>
                    <Input
                      type="email"
                      id="email"
                      {...form.register("email", { required: true })}
                    />
                  </FormControl>
                </FormItem>

                {/* Password Input */}
                <FormItem>
                  <FormLabel>Password</FormLabel>
                  <FormControl>
                    <Input
                      type="password"
                      id="password"
                      {...form.register("password", { required: true })}
                    />
                  </FormControl>
                </FormItem>

                {/* Login Button */}
                <Button type="submit" className="bg-green-600 w-full">
                  Login
                </Button>
              </form>
            </Form>

            {/* Gmail Login Button */}
            <div className="mt-4">
              <Button
                variant="outline"
                className="w-full flex items-center justify-center gap-2"
              >
                <img
                  src="https://upload.wikimedia.org/wikipedia/commons/4/4e/Gmail_Icon.svg"
                  alt="Gmail Logo"
                  className="w-5 h-5"
                />
                Sign in with Gmail
              </Button>
            </div>

            {/* Close Button */}
            <Button variant="ghost" className="w-full mt-4" onClick={closePopup}>
              Close
            </Button>
          </div>
        </div>
      )}
    </>
  );
}